.popup-form-extra {
	position: absolute;
	width: 480px;
	left: calc(50% - 480px/2 + 1px);
	top: 138px;

	background: #FFFFFF;
	box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
	border-radius: 3px;

	z-index: 10;
}

.extra-wrapper {
	position: relative;

	display: flex;
	flex-direction: column;
	flex-wrap: wrap;

	margin: 0 auto;
	width: 400px;
	margin-top: 40px;
	margin-bottom: 40px;
}

.form-extra-heading {
	margin-bottom: 30px;
}

.extra-heading {
	margin: 0;
	font-family: "Roboto-bold";
	font-style: normal;
	font-weight: bold;
	font-size: 14px;
	line-height: 16px;
	padding-bottom: 15px;
}

.inputs {
	display: flex;
}

.sex-inputs {
	width: 250px;
	flex-direction: row;
	flex-wrap: wrap;
}

.sex {
	margin-right: 20px;
}

.sex-label {
	margin-right: 3px;
}

.sex:nth-child(2n-1) {
	margin-right: 0px;
}

.education-inputs {
	flex-direction: column;
}

.info-area {
	resize: none;
}

.subscription-inputs {
	flex-direction: row;
	flex-wrap: wrap;
	width: 100px;
}

.sport,
.science {
	width: 16px;
}

.input-education,
.input-subscription {
	margin-bottom: 10px;
}

.input-education:last-child,
.input-subscription:last-child {
	margin-bottom: none;
}

.popup-close-extra {
	position: absolute;
  top: -5px;
  right: 9px;
  width: 22px;
  height: 22px;
  font-size: 0;
  background-color: transparent;
  border: 0;
  cursor: pointer;
}

.popup-close-extra:hover::before,
.popup-close-extra:hover::after {
	background-color: #558CB7;
}

.popup-close-extra::before {
	content: "";
  position: absolute;
  top: 10px;
  left: 2px;
  width: 22px;
  height: 3px;
  background-color: #C4C4C4;
  transform: rotate(45deg);
}

.popup-close-extra::after {
	content: "";
  position: absolute;
  top: 10px;
  left: 2px;
  width: 22px;
  height: 3px;
  background-color: #C4C4C4;
  transform: rotate(-45deg);
}
