.button__open-form {
	position: absolute;
	top: calc(50% - 20px);
left:calc(50% - 202px/2 - 0.5px);
	padding: 13px 59px;

	font-family: "Roboto";
	font-style: normal;
	font-weight: bold;
	font-size: 14px;
	line-height: 16px;

	color: #fff;
	background: #558CB7;
	border-radius: 3px;
	border: none;

	cursor: pointer;
}

.button__open-form:hover {
	opacity: 0.8;
}

.button__open-form:active {
	opacity: 0.5;
}