.popup__form {
	position: absolute;
	width: 480px;
	left: calc(50% - 480px/2 + 1px);
	top: 138px;

	background: #FFFFFF;
	box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
	border-radius: 3px;

	z-index: 10;
}

.form__wrapper {
	position: relative;

	display: flex;
	flex-direction: column;
	flex-wrap: wrap;

	margin: 0 auto;
	width: 400px;
	margin-top: 40px;
	margin-bottom: 40px;
}

.popup-close {
	position: absolute;
  top: -5px;
  right: 9px;
  width: 22px;
  height: 22px;
  font-size: 0;
  background-color: transparent;
  border: 0;
  cursor: pointer;
}

.popup-close:hover::before,
.popup-close:hover::after {
	background-color: #558CB7;
}

.popup-close::before {
	content: "";
  position: absolute;
  top: 10px;
  left: 2px;
  width: 22px;
  height: 3px;
  background-color: #C4C4C4;
  transform: rotate(45deg);
}

.popup-close::after {
	content: "";
  position: absolute;
  top: 10px;
  left: 2px;
  width: 22px;
  height: 3px;
  background-color: #C4C4C4;
  transform: rotate(-45deg);
}

.form-heading {
	margin: 0px;
	margin-bottom: 30px;

	font-family: "Roboto";
	font-style: normal;
	font-weight: normal;
	font-size: 24px;
	line-height: 28px;
}

.form-input {
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	margin: 0px;
	margin-bottom: 25px;
}

.form-input__password {
	margin-bottom: 4px;
}

.label {
	font-family: "Roboto-bold";
	font-style: normal;
	font-weight: bold;
	font-size: 14px;
	line-height: 16px;
	padding-bottom: 11px;
}

.input {
	border: 1px solid #DDDDDD;
	width: 100%;
	padding: 12px 20px;

	font-family: "Roboto";
	font-style: normal;
	font-weight: normal;
	font-size: 14px;
	line-height: 16px;
}

.input:hover {
	border: 1px solid #82B3D9;
}

.rules-heading {
	margin: 0px;
	margin-bottom: 4px;

	font-family: "Roboto-bold";
	font-style: normal;
	font-weight: bold;
	font-size: 12px;
	line-height: 14px;

	color: #777777;
}

.rules-item {
	display: flex;
	flex-wrap: wrap;
	margin-bottom: 3px;
}

.rules-item:last-child {
	margin-bottom: 30px;
}

.rule-indicator {
	background: url(../img/icons/indicator-normal.svg);
	width: 16px;
	height: 16px;
}

.rule-error {
	background: url(../img/icons/indicator-error.svg);
}

.rule-ok {
	background: url(../img/icons/indicator-ok.svg);
}


.rule__text {
	font-family: "Roboto";
	font-style: normal;
	font-weight: normal;
	font-size: 12px;
	line-height: 14px;

	color: #777777;
}

.confirm-password {
	margin-bottom: 33px;
	position: relative;
}

.password-error {
	position: absolute;

  left: 0px;
  right: 0px;
  top: -25px;

  font-family: "Roboto";
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;

  color: #9F2B11;
}

.password-match {
	position: absolute;

  left: 0px;
  right: 0px;
  top: 70px;

  font-family: "Roboto";
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;

  color: #9F2B11;
}

.confirm {
	display: flex;
	flex-wrap: wrap;
}

.confirm__checkbox {
	margin-left: 18px;

  cursor: pointer;
}

.license-link:hover  {
	text-decoration: underline;
}

.confirm-checkbox input[type="checkbox"]:hover + .confirm-indicator {
	border: 1px solid #558CB7;
}

.confirm-checkbox {
	position: relative;
	cursor: pointer;
}

.confirm-checkbox input[type="checkbox"] + .confirm-indicator {
	position: absolute;
  top: 3px;
  left: -17px;
  width: 12px;
  height: 12px;
  border: 1px solid #DDDDDD;
  border-radius: 3px;
}

.confirm-checkbox input[type="checkbox"]:checked + .confirm-indicator {
	background: #558CB7;
	border: 1px solid #558CB7;
}

.confirm-checkbox input[type="checkbox"]:checked + .confirm-indicator::before {
	content: "";
  position: absolute;
  top: 5px;
  left: 0px;
  width: 5px;
  height: 2px;
  background: #fff;
  transform: rotate(45deg);
}

.confirm-checkbox input[type="checkbox"]:checked + .confirm-indicator::after {
	content: "";
  position: absolute;
  top: 4px;
  left: 2px;
  width: 8px;
  height: 2px;
  background: #fff;
  transform: rotate(-45deg);
}

.confirm-text {
	font-family: "Roboto";
	font-style: normal;
	font-weight: normal;
	font-size: 12px;
	line-height: 14px;

	color: #000000;
}

.license-link {
	color: #7fa8c9;
	text-decoration: none;
}

.send {
	display: flex;
	justify-content: center;
	margin-top: 39px;
}

.button-send {
	padding: 13px 30px;
	font-family: "Roboto";
	font-style: normal;
	font-weight: bold;
	font-size: 14px;
	line-height: 16px;

	color: #FFFFFF;
	background: #82B3D9;
	border-radius: 3px;
}


