body {
  max-width: 1440px;
  margin: 0 auto;

  font-family: "Roboto", "Arial", sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
   
  background: #E5E5E5;
}


html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.hidden {
	display: none;
}

.button:hover {
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.4);
}

.button:active {
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.4);
}

.button:disabled {
  background: #82B3D9;
}