@font-face {
  font-family: "Roboto";

  font-weight: normal;
  font-style: normal;
  font-display: swap;

  src: url("../fonts/roboto.woff2") format("woff2"),
    url("../fonts/roboto.woff") format("woff");
}

@font-face {
  font-family: "Roboto-bold";

  font-weight: normal;
  font-style: normal;
  font-display: swap;

  src: url("../fonts/roboto-bold.woff2") format("woff2"),
    url("../fonts/roboto-bold.woff") format("woff");
}



